@import url(https://fonts.googleapis.com/css2?family=Kurale&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cabin&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --body-background-color: #e6e6ff;
  --page-background-color: #f5f5f5;

  --baseline: 12px;
}

.front-cover {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.cover .book-title {
  font-family: 'Cabin', sans-serif;
  font-size: 3em;
  color: whitesmoke;
  position: absolute;
  bottom: 8%;
  right: 5%;
}

.page .book-title {
  margin-top: 30%;
  text-align: center;
  width: 100%;
  font-size: 3.5em;
  border-top: 2px solid black;
}

.page .illustration {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 80%;
  height: auto;
}

.page .image-footer {
  position: absolute;
  padding-right: 4em;
  bottom: 4em;
  text-align: center;
  font-size: 0.6em;
  font-style: italic;
}

.cover .book-author {
  font-family: 'Cabin', sans-serif;
  position: absolute;
  bottom: 3%;
  right: 5%;
  color: whitesmoke;
}

.page .book-author {
  width: 100%;
  border-bottom: 2px solid black;
  font-size: 1.5em;
  text-align: center;
  padding-bottom: 0.5em;
}

p {
  text-align: justify;
  -ms-text-justify: distribute-all-lines;
  text-justify: distribute-all-lines;
  font-size: 0.8em;
}

p.broken::after {
  display: inline-block;
  width: 100%;
  content: '.';
  font-size: 0;
  line-height: 0;
  visibility: hidden;
}

p.opening {
  margin-top: 40%;
}

p:not(.opening) {
  margin-top: 0.5em;
}

p:not(.opening):not(.trailing):not(.copyright):not(.permission)::first-letter {
  margin-left: 1em;
}

p.opening::first-letter {
  font-size: 4em;
  float: left;
  margin: -0.3em 0.2em;
}

.footer {
  position: absolute;
  bottom: 1.5em;
  padding-top: 0.5em;
  width: 82%;
  border-top: 1px solid black;
}

.footer.odd {
  text-align: right;
}

.footer.even {
  text-align: left
}

.header {
  position: absolute;
  top: 1.5em;
  padding-bottom: 0.5em;
  font-style: italic;
  width: 82%;
}

.header.title {
  text-align: right;
}

.header.author {
  text-align: left;
}

.header, .footer {
  font-size: 0.6em;
}

.content {
  font-family: 'Kurale', serif;
  padding: 3em 3em 4em 3em;
}

.content .turtle {
  position: fixed;
  top: 60%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 50%;
  height: auto;
}

.back-cover {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 50%;
  height: auto;
}

.cover-background {
  background-color: darkolivegreen;
}

.cover {
  background-color: darksalmon;
}

p.copyright {
  padding-top: 60%;
}

p.copyright, p.permission {
  font-size: 0.6em;
}

.end {
  padding-top: 1em;
  text-align: center;
}
.noselect {
  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */ /* Konqueror HTML */ /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */
}

.clickable {
  cursor: pointer;
}

.App {
  background-color: var(--body-background-color);
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.content {
  width: 100%;
  max-width: 100%;
  height: auto;
}

:root {
  --desk-width: calc(var(--baseline) * 60);
  --desk-height: calc(var(--baseline) * 42.6);
}

.desk {
  width: calc(var(--baseline) * 60);
  width: var(--desk-width);
  height: calc(var(--baseline) * 42.6);
  height: var(--desk-height);
}

.desk.empty {
  box-shadow: 0 0 100px rgba(0, 0, 0, 0.3);
  display: flex;
  align-items: center;
  justify-content: center;
}

.book {
  width: 100%;
  height: 100%;
  display: flex;
  perspective: calc(var(--baseline) * 100);
}

.container {
  position: relative;
  width: 50%;
  height: 100%;
  display: grid;
  transform: rotateY(0deg);
  transition: transform 0.9s cubic-bezier(0.645, 0.045, 0.355, 1);
  transform-origin: 0% 0%;
}



.container.page {
  background-color: var(--page-background-color);
}

.container.empty {
  background-color: transparent;
}

#next:not(.cover):not(.empty), .container.turnable {
  background-image: linear-gradient(90deg, #e3e3e3 0%, rgba(247, 247, 247, 0) 18%);
}

#previous:not(.cover):not(.empty) {
  background-image: linear-gradient(-90deg, #e3e3e3 0%, rgba(247, 247, 247, 0) 18%);
}

.container.turnable {
  position: absolute;
  right: 0;
  pointer-events: none;
  transform-style: preserve-3d;
}

.container.hidden {
  visibility: hidden;
}

.container.turnable.turned {
  transition: transform 0.9s cubic-bezier(0.645, 0.045, 0.355, 1);
  transform: rotateY(-180deg);
}

.container.cover.turnable {
  background-image: none;
}

.front {
  position: absolute;
  width: 100%;
  height: 100%;
  transform: rotateY(0deg) translateZ(1px);
}

.back {
  position: absolute;
  width: 100%;
  height: 100%;
  transform: rotateY(180deg) translateZ(1px);
}

.clickable {
  cursor: pointer;
}
