@import url('https://fonts.googleapis.com/css2?family=Kurale&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Cabin&display=swap');

:root {
  --body-background-color: #e6e6ff;
  --page-background-color: #f5f5f5;

  --baseline: 12px;
}

.front-cover {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.cover .book-title {
  font-family: 'Cabin', sans-serif;
  font-size: 3em;
  color: whitesmoke;
  position: absolute;
  bottom: 8%;
  right: 5%;
}

.page .book-title {
  margin-top: 30%;
  text-align: center;
  width: 100%;
  font-size: 3.5em;
  border-top: 2px solid black;
}

.page .illustration {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 80%;
  height: auto;
}

.page .image-footer {
  position: absolute;
  padding-right: 4em;
  bottom: 4em;
  text-align: center;
  font-size: 0.6em;
  font-style: italic;
}

.cover .book-author {
  font-family: 'Cabin', sans-serif;
  position: absolute;
  bottom: 3%;
  right: 5%;
  color: whitesmoke;
}

.page .book-author {
  width: 100%;
  border-bottom: 2px solid black;
  font-size: 1.5em;
  text-align: center;
  padding-bottom: 0.5em;
}

p {
  text-align: justify;
  -ms-text-justify: distribute-all-lines;
  text-justify: distribute-all-lines;
  font-size: 0.8em;
}

p.broken::after {
  display: inline-block;
  width: 100%;
  content: '.';
  font-size: 0;
  line-height: 0;
  visibility: hidden;
}

p.opening {
  margin-top: 40%;
}

p:not(.opening) {
  margin-top: 0.5em;
}

p:not(.opening):not(.trailing):not(.copyright):not(.permission)::first-letter {
  margin-left: 1em;
}

p.opening::first-letter {
  font-size: 4em;
  float: left;
  margin: -0.3em 0.2em;
}

.footer {
  position: absolute;
  bottom: 1.5em;
  padding-top: 0.5em;
  width: 82%;
  border-top: 1px solid black;
}

.footer.odd {
  text-align: right;
}

.footer.even {
  text-align: left
}

.header {
  position: absolute;
  top: 1.5em;
  padding-bottom: 0.5em;
  font-style: italic;
  width: 82%;
}

.header.title {
  text-align: right;
}

.header.author {
  text-align: left;
}

.header, .footer {
  font-size: 0.6em;
}

.content {
  font-family: 'Kurale', serif;
  padding: 3em 3em 4em 3em;
}

.content .turtle {
  position: fixed;
  top: 60%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 50%;
  height: auto;
}

.back-cover {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 50%;
  height: auto;
}

.cover-background {
  background-color: darkolivegreen;
}

.cover {
  background-color: darksalmon;
}

p.copyright {
  padding-top: 60%;
}

p.copyright, p.permission {
  font-size: 0.6em;
}

.end {
  padding-top: 1em;
  text-align: center;
}