@import url(../../App.css);

:root {
  --desk-width: calc(var(--baseline) * 60);
  --desk-height: calc(var(--baseline) * 42.6);
}

.desk {
  width: var(--desk-width);
  height: var(--desk-height);
}

.desk.empty {
  box-shadow: 0 0 100px rgba(0, 0, 0, 0.3);
  display: -webkit-box;
  display: flex;
  align-items: center;
  justify-content: center;
}

.book {
  width: 100%;
  height: 100%;
  display: -webkit-box;
  display: flex;
  -webkit-perspective: calc(var(--baseline) * 100);
  perspective: calc(var(--baseline) * 100);
}

.container {
  position: relative;
  width: 50%;
  height: 100%;
  display: grid;
  -webkit-transform: rotateY(0deg);
          transform: rotateY(0deg);
  -webkit-transition: -webkit-transform 0.9s cubic-bezier(0.645, 0.045, 0.355, 1);
  transition: -webkit-transform 0.9s cubic-bezier(0.645, 0.045, 0.355, 1);
  transition: transform 0.9s cubic-bezier(0.645, 0.045, 0.355, 1);
  transition: transform 0.9s cubic-bezier(0.645, 0.045, 0.355, 1), -webkit-transform 0.9s cubic-bezier(0.645, 0.045, 0.355, 1);
  -webkit-transform-origin: 0% 0%;
          transform-origin: 0% 0%;
}



.container.page {
  background-color: var(--page-background-color);
}

.container.empty {
  background-color: transparent;
}

#next:not(.cover):not(.empty), .container.turnable {
  background-image: -webkit-gradient(linear, left top, right top, from(#e3e3e3), color-stop(18%, rgba(247, 247, 247, 0)));
  background-image: linear-gradient(90deg, #e3e3e3 0%, rgba(247, 247, 247, 0) 18%);
}

#previous:not(.cover):not(.empty) {
  background-image: -webkit-gradient(linear, right top, left top, from(#e3e3e3), color-stop(18%, rgba(247, 247, 247, 0)));
  background-image: linear-gradient(-90deg, #e3e3e3 0%, rgba(247, 247, 247, 0) 18%);
}

.container.turnable {
  position: absolute;
  right: 0;
  pointer-events: none;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}

.container.hidden {
  visibility: hidden;
}

.container.turnable.turned {
  -webkit-transition: -webkit-transform 0.9s cubic-bezier(0.645, 0.045, 0.355, 1);
  transition: -webkit-transform 0.9s cubic-bezier(0.645, 0.045, 0.355, 1);
  transition: transform 0.9s cubic-bezier(0.645, 0.045, 0.355, 1);
  transition: transform 0.9s cubic-bezier(0.645, 0.045, 0.355, 1), -webkit-transform 0.9s cubic-bezier(0.645, 0.045, 0.355, 1);
  -webkit-transform: rotateY(-180deg);
          transform: rotateY(-180deg);
}

.container.cover.turnable {
  background-image: none;
}

.front {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-transform: rotateY(0deg) translateZ(1px);
          transform: rotateY(0deg) translateZ(1px);
}

.back {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-transform: rotateY(180deg) translateZ(1px);
          transform: rotateY(180deg) translateZ(1px);
}

.clickable {
  cursor: pointer;
}